import Presenter from './presenter.js';

/**
 * @extends {Presenter<OverlayView>}
 */
class OverlayPresenter extends Presenter {
  /**
   * @type {boolean }
   */
  isModelBusy;

  /**
   * @override
   */
  addEventListeners() {
    this.model.addEventListener('busy', this.modelBusyHandler.bind(this));
    this.model.addEventListener('idle', this.modelIdleHandler.bind(this));
  }

  modelBusyHandler() {
    this.isModelBusy = true;
    this.updateView();
  }

  modelIdleHandler() {
    this.isModelBusy = false;
    this.updateView();
  }

  /**
   * @override
   * @return {OverlayViewState}
   */
  createViewState() {
    return {
      isActive: this.isModelBusy,
    };
  }


}

export default OverlayPresenter;
